import { http } from '@/http';
import { PickerFieldNames } from "vant";
import { PickerObjectOption } from "vant";
import Toast from "vant/es/toast";
import "vant/es/toast/style/less";
import { computed, defineComponent, onMounted, ref } from 'vue';
import { useStore } from 'vuex';
export default defineComponent({
    name: 'CustomerUserInfo',
    setup() {
        const store = useStore();
        const needComplete = computed(() => store.state.needComplete);
        const fanHeadImg = ref('');
        const fanNickName = ref('');
        const fanGender = ref('');
        const birthday = ref('');
        const fanMobile = ref('');
        const phonePattern = /^1[3456789]\d{9}$/;
        const phoneCodePattern = /^\d{6}$/;
        const showDatetimePicker = ref(false);
        const showMalePicker = ref(false);
        const currentDate = ref(new Date());
        const minDate = ref(new Date(1920, 0, 1));
        const maxDate = ref(new Date());
        const maleColumns = [
            { name: '男', id: '1' },
            { name: '女', id: '2' }
        ];
        const maleCustomFieldName = {
            text: 'name'
        };
        const maleIndex = ref(0);
        const phoneCode = ref('');
        const phoneCodeText = ref('发送验证码');
        const codeCount = ref(60);
        const btnDisabled = ref(false);
        const fanGenderFlag = ref(false);
        const birthdayFlag = ref(false);
        const fanMobileFlag = ref(false);
        const jumpToAddr = () => {
            window.location.href = '/yx/views/general/addr-mgr.html';
        };
        const getUserInfo = () => {
            http
                .post('/hbSeller/fans/personalInfo/query', {}, false)
                .then((res) => {
                if (res.code === '200') {
                    fanHeadImg.value = res.data.info.fanHeadImg;
                    fanNickName.value = res.data.info.fanNickName;
                    birthday.value = res.data.info.birthday || '';
                    fanMobile.value = res.data.info.fanMobile;
                    fanGender.value =
                        res.data.info.fanGender == 1
                            ? '男'
                            : res.data.info.fanGender == 2
                                ? '女'
                                : '';
                    fanHeadImg.value = res.data.info.fanHeadImg;
                    if (fanGender.value)
                        fanGenderFlag.value = true;
                    if (birthday.value)
                        birthdayFlag.value = true;
                    if (fanMobile.value)
                        fanMobileFlag.value = true;
                }
                else {
                    Toast.fail(res.msg);
                }
            })
                .catch((err) => {
                console.log(err);
            });
        };
        const handleClickField = (flag) => {
            if (flag === 'showDatetimePicker') {
                showDatetimePicker.value = true;
            }
            if (flag === 'showMalePicker') {
                showMalePicker.value = true;
            }
        };
        const onDatetimeConfirm = (value) => {
            const d = new Date(value);
            birthday.value =
                d.getFullYear() +
                    '-' +
                    (d.getMonth() + 1 < 10 ? '0' + (d.getMonth() + 1) : d.getMonth() + 1) +
                    '-' +
                    (d.getDate() - 0 < 10 ? '0' + d.getDate() : d.getDate());
            showDatetimePicker.value = false;
            console.log(value);
        };
        let gender;
        const onMaleConfirm = (value) => {
            fanGender.value = value.name;
            gender = value.id;
            showMalePicker.value = false;
            console.log(gender);
        };
        const handleCodeChange = () => {
            if (phoneCode.value.length > 6) {
                phoneCode.value = phoneCode.value.substring(0, 6);
            }
        };
        const handleGetCode = () => {
            if (!phonePattern.test(fanMobile.value)) {
                Toast.fail('请输入正确的手机号！');
                return;
            }
            http
                .post('/syx/user/bind/svcode', { mobile: fanMobile.value }, false)
                .then((res) => {
                if (res.code === '200') {
                    Toast.success('已发送！');
                    btnDisabled.value = true;
                    phoneCodeText.value = `重新发送(${codeCount.value--})`;
                    // eslint-disable-next-line @typescript-eslint/no-explicit-any
                    let countInter = setInterval(() => {
                        phoneCodeText.value = `重新发送(${codeCount.value--})`;
                        if (codeCount.value < 0) {
                            clearInterval(countInter);
                            countInter = undefined;
                            codeCount.value = 60;
                            phoneCodeText.value = '发送验证码';
                            btnDisabled.value = false;
                        }
                    }, 1000);
                }
                else {
                    Toast.fail(res.msg);
                }
            })
                .catch((err) => {
                console.log(err);
            });
        };
        const completeUserInfo = (params) => {
            return new Promise((resolve) => {
                http
                    .post('/spoint/member/completeUserInfo', params, false)
                    .then((res) => {
                    resolve(res);
                });
            });
        };
        const handleSubmitCode = (params) => {
            return new Promise((resolve) => {
                http.post('/syx/user/bind/mobile', params, false).then((res) => {
                    resolve(res);
                });
            });
        };
        const handleSyncUserInfo = () => {
            http.post('/hbSeller/fans/syncUserInfo', {}, false).then((res) => {
                console.log(res);
                if (res.code === '200') {
                    store.commit('setNeedComplete', false);
                    sessionStorage.setItem('needComplete', '0');
                    Toast.success('保存成功!');
                    setTimeout(() => {
                        getUserInfo();
                    }, 3000);
                }
            });
        };
        const onSubmit = () => {
            if (!gender) {
                if (fanGender.value === '男') {
                    gender = '1';
                }
                else {
                    gender = '2';
                }
            }
            const params1 = {
                img: null,
                nickname: null,
                birth: birthday.value,
                gender
            };
            const params2 = {
                mobile: fanMobile.value,
                vcode: phoneCode.value
            };
            const httpArr = [];
            if (!(fanGenderFlag.value && birthdayFlag.value)) {
                httpArr.push(completeUserInfo(params1));
            }
            if (!fanMobileFlag.value) {
                httpArr.push(handleSubmitCode(params2));
            }
            console.log(params1, params2);
            Promise.all(httpArr).then(
            // eslint-disable-next-line @typescript-eslint/no-explicit-any
            (resArr) => {
                if (resArr.length === 2) {
                    const [res1, res2] = resArr;
                    if (res1.code == 200 && res2.code == 200) {
                        handleSyncUserInfo();
                    }
                    else {
                        const msg = `${res1.msg ? res1.msg : ''}${res1.msg && res2.msg ? ',' : ''}${res2.msg ? res2.msg : ''}`;
                        Toast.fail(msg);
                    }
                }
                else {
                    const [res1] = resArr;
                    if (res1.code == 200) {
                        handleSyncUserInfo();
                    }
                    else {
                        Toast.fail(`${res1.msg}`);
                    }
                }
            });
        };
        onMounted(() => {
            getUserInfo();
            Toast.success('保存成功!');
        });
        return {
            needComplete,
            fanHeadImg,
            fanNickName,
            birthday,
            fanMobile,
            fanGender,
            jumpToAddr,
            phonePattern,
            handleClickField,
            showDatetimePicker,
            showMalePicker,
            currentDate,
            minDate,
            maxDate,
            maleColumns,
            maleCustomFieldName,
            maleIndex,
            phoneCode,
            phoneCodePattern,
            phoneCodeText,
            codeCount,
            btnDisabled,
            fanGenderFlag,
            birthdayFlag,
            fanMobileFlag,
            handleGetCode,
            handleCodeChange,
            onDatetimeConfirm,
            onMaleConfirm,
            onSubmit
        };
    }
});
